.Game {
  text-align: center;
  padding: 1rem;
  font-family: "Comic Sans MS", sans-serif;
  margin: 0 auto;
}

.Game * {
  box-sizing: border-box;
}

.Game button {
  padding: 0.5rem;
  font-family: "Comic Sans MS", sans-serif;
  background: transparent;
  border: dashed 0.2rem rgb(0, 0, 0);
  border-radius: 15rem 0.9375rem 15rem 0.9375rem/0.9375rem 15rem 0.9375rem 15rem;
  outline: none;
}

.Game .Definition {
  font-size: 1.5rem;
}

.Letter {
  font-size: 1.5rem;
  font-family: "Comic Sans MS", sans-serif;
  background: transparent;
  border-radius: 15rem 0.9375rem 15rem 0.9375rem/0.9375rem 15rem 0.9375rem 15rem;
  border: solid 0.3125rem rgb(0, 0, 0);
}

.Letter:focus,
.Game button:focus {
  background-color: yellow;
}

.Letter,
.Game button {
  margin: 0 0.25rem;
}

.Letter.correct {
  background-color: green;
}

.Letter.incorrect {
  background-color: red;
}

.Word {
  font-size: 2rem;
}
